body{background-color:#73fbf8;display:flex;justify-content:center;align-items:center;flex-direction:column}.navbar{display:flex;flex-direction:row;justify-content:space-between;align-items:center;background-color:black;min-width:1400px;margin-bottom:50px;font-family:'Press Start 2P', cursive}.navbar a{text-decoration:none;color:#73fbf8}.navbar h3{color:yellow}.arcade{background-color:black;width:1290px;height:1000px;border-left:30px solid #a29d9d;border-right:30px solid #a29d9d;border-bottom:2px solid #a29d9d;border-radius:10px;display:flex;align-items:center;flex-direction:column}.title{background-color:#d7d6dd;font-family:'Press Start 2P', cursive;width:1200px;height:225px;margin-top:35px;display:flex;align-items:center;justify-content:center}.title h1{color:black;-webkit-text-stroke:1px yellow;font-size:40px}#score{color:yellow;font-family:'Press Start 2P', cursive;font-size:larger}.arcade-display{width:1290px;height:700px;display:flex;justify-content:space-evenly;align-items:center}.arcade-display::after{content:" ";display:block;position:absolute;top:50%;left:5%;bottom:0;right:0;background:rgba(18,16,16,0.1);height:450px;width:1287px;opacity:0;z-index:2;pointer-events:none}.arcade-display::before{content:" ";display:block;position:absolute;top:50%;left:5%;bottom:0;right:0;background:linear-gradient(rgba(18,16,16,0) 50%, rgba(0,0,0,0.25) 50%),linear-gradient(90deg, rgba(255,0,0,0.06), rgba(0,255,0,0.02), rgba(0,0,255,0.06));z-index:2;background-size:100% 2px, 3px 100%;height:450px;width:1287px;pointer-events:none}.arcade-display p{border:1px solid #d7d6dd;border-radius:6px;width:225px;height:390px;display:flex;justify-content:center;align-items:center;text-align:center;color:yellow;font-family:'Press Start 2P', cursive}#canvas{border:1px solid #d7d6dd;width:800px;height:400px;border-radius:10px;background-image:url(images/deathstar.gif);-webkit-animation:turn-on 2s linear;animation:turn-on 2s linear;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}#splash{border:1px solid #d7d6dd;width:800px;height:400px;border-radius:10px;background-image:url(images/plans.gif);background-position:center;background-repeat:no-repeat}#endgame{border:1px solid #d7d6dd;width:800px;height:400px;border-radius:10px;background-image:url(images/iLSuiKP.gif);background-position:center;background-repeat:no-repeat;-webkit-animation:turn-on 2s linear;animation:turn-on 2s linear;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}#gameover{border:1px solid #d7d6dd;width:800px;height:400px;border-radius:10px;background-image:url(images/gameover.gif);background-position:center;background-repeat:no-repeat;-webkit-animation:turn-on 2s linear;animation:turn-on 2s linear;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}#x-wing{display:none}#tie{display:none}#deathstar{display:none}#explosion{display:none}.coin{background-color:#a29d9d;margin-bottom:50px;width:150px;height:200px;border:1px solid #d7d6dd;border-radius:3px;display:flex;flex-direction:row;justify-content:center;align-items:center}.coin-slot{background-color:black;width:10px;height:120px;margin-left:2px;margin-right:5px}#switch{display:none}#switch:hover{cursor:pointer}#switch:checked+.coin-button{-webkit-animation:none;animation:none;background-color:#660b0d}.coin-button{background-color:violet;width:120px;height:120px;display:flex;justify-content:center;align-items:center;text-align:center;font-family:'Press Start 2P', cursive;border-radius:3px;cursor:pointer;-webkit-animation:glowing 1300ms infinite;animation:glowing 1300ms infinite}@-webkit-keyframes glowing{0%{background-color:#660b0d;box-shadow:0 0 15px #660b0d}50%{background-color:#fdec91;box-shadow:0 0 25px #fdec91}100%{background-color:#660b0d;box-shadow:0 0 15px #660b0d}}@keyframes glowing{0%{background-color:#660b0d;box-shadow:0 0 15px #660b0d}50%{background-color:#fdec91;box-shadow:0 0 25px #fdec91}100%{background-color:#660b0d;box-shadow:0 0 15px #660b0d}}@-webkit-keyframes turn-on{0%{transform:scale(1, 0.8) translate3d(0, 0, 0);filter:brightness(30);opacity:1}3.5%{transform:scale(1, 0.8) translate3d(0, 100%, 0)}3.6%{transform:scale(1, 0.8) translate3d(0, -100%, 0);opacity:1}9%{transform:scale(1.3, 0.6) translate3d(0, 100%, 0);filter:brightness(30);opacity:0}11%{transform:scale(1, 1) translate3d(0, 0, 0);filter:contrast(0) brightness(0);opacity:0}100%{transform:scale(1, 1) translate3d(0, 0, 0);filter:contrast(1) brightness(1.2) saturate(1.3);opacity:1}}@keyframes turn-on{0%{transform:scale(1, 0.8) translate3d(0, 0, 0);filter:brightness(30);opacity:1}3.5%{transform:scale(1, 0.8) translate3d(0, 100%, 0)}3.6%{transform:scale(1, 0.8) translate3d(0, -100%, 0);opacity:1}9%{transform:scale(1.3, 0.6) translate3d(0, 100%, 0);filter:brightness(30);opacity:0}11%{transform:scale(1, 1) translate3d(0, 0, 0);filter:contrast(0) brightness(0);opacity:0}100%{transform:scale(1, 1) translate3d(0, 0, 0);filter:contrast(1) brightness(1.2) saturate(1.3);opacity:1}}@-webkit-keyframes turn-off{0%{transform:scale(1, 1.3) translate3d(0, 0, 0);filter:brightness(1);opacity:1}60%{transform:scale(1.3, 0.001) translate3d(0, 0, 0);filter:brightness(10)}100%{-webkit-animation-timing-function:cubic-bezier(0.755, 0.05, 0.855, 0.06);animation-timing-function:cubic-bezier(0.755, 0.05, 0.855, 0.06);transform:scale(0, 0.0001) translate3d(0, 0, 0);filter:brightness(50)}}@keyframes turn-off{0%{transform:scale(1, 1.3) translate3d(0, 0, 0);filter:brightness(1);opacity:1}60%{transform:scale(1.3, 0.001) translate3d(0, 0, 0);filter:brightness(10)}100%{-webkit-animation-timing-function:cubic-bezier(0.755, 0.05, 0.855, 0.06);animation-timing-function:cubic-bezier(0.755, 0.05, 0.855, 0.06);transform:scale(0, 0.0001) translate3d(0, 0, 0);filter:brightness(50)}}
